.cardSection{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 5em;
    gap: 5em;
    margin: 2em;
    overflow-x: auto;
}
.cardSection > .card{
    color: aliceblue;
    height: 400px;
    min-width: 300px;
    max-width: 400px;
    padding: auto 1em;
    background-color: var(--blue);
    border-bottom-right-radius: 1.5em;
    border-bottom-left-radius: 1.5em;
    position: relative;
}
.cardSection > .card:hover{
    border: 1px solid var(--prim-blue);
}
.cardSection > .card > h3{
    color: #ffffff;
    background-color: var(--prim-blue);
    margin: auto auto 1em auto;
    padding: 1em;
    border-bottom-right-radius: 2em;
}
.cardSection > .card > .card-bottom{
    display: grid;
    grid-template-columns: repeat(1,2fr);
    grid-gap: 3em;
    gap: 3em;
}
.cardSection > .card > .card-bottom > ul{
    text-align: start;
    margin-left: 1.5em;
    line-height: 2em;
}
.cardSection > .card > .card-bottom  > ul > li {
    color: var(--prim-blue);
    font-size: 1.2em;
}
.cardSection > .card > .card-bottom  > p {
    color: var(--prim-blue-var-light);
    font-size: 1.2em;
}
.cardSection > .card > .card-bottom  > p >span{
    font-size: 2em;
    position: relative;
}
.cardSection > .card > .card-bottom  > p >span::before{
    position: absolute;
    content: '$';
    top:0;
    left:-.7em;
    font-size: .6em;
}
