/* hambuger styling */
.header{
    position: fixed;
    z-index: 1000;
    left: 0;
    top: 0;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.nav-list ul{ 
    list-style: none;
    position: absolute;
    background:var(--bg-color) ;
    width: 100vw;
    height: 100vh;
    left: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
    z-index: 1;
    transition: .4s ease left;
    overflow: hidden; 
}

.nav-list ul.active{
    left: 0;
}
.nav-list ul li{
    background: transparent;
}
.nav-list ul a{
    font-size: 1.5rem;
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: .2rem;
    padding: 20px;
    display: block;
    color: var(--prim-blue);
}
#hambuger{
    z-index: 100;
    height: 35px;
    width: 35px;
    display: inline-block;
    border: 2px solid white;
    border-radius: 50%;
    display: flex;
    position: relative;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    margin: 1em;
}
#hambuger::after{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border:3px solid white ;
    -webkit-animation: hambuger_puls 1s ease infinite;
            animation: hambuger_puls 1s ease infinite;
}
@-webkit-keyframes hambuger_puls{
    0%{
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        opacity: 0;
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
@keyframes hambuger_puls{
    0%{
        opacity: 1;
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    100%{
        opacity: 0;
        -webkit-transform: scale(1.5);
                transform: scale(1.5);
    }
}
.bar{
    height: 2px;
    width: 27px;
    position: relative;
    background: white;
    z-index: -1;
}
.bar::after,
.bar::before{
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    background: #fff;
    transition: .3s ease;
    transition-property: top,bottom;
}
.bar::after{
    top: 7px;
}
.bar::before{
    bottom: 7px;
}
#hambuger.active .bar::before{
    bottom: 0;
}
#hambuger.active .bar::after{
    top: 0;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    list-style: none;
    font-family: 'Inter', sans-serif;
}

:root{
    --bg-color:#1f1f3b;
    --blue:#2c2c6c;
    --prim-blue:#4db5ff;
    --prim-blue-var:rgba(77, 181, 255, 0.4);
    --prim-blue-var-light:rgba(255, 255, 255, 0.7);
}
html{
    scroll-behavior: smooth;
}
body{
    background-color: #1f1f3b;
    background-color: var(--bg-color);
}
section{
    padding-top: 2.5em;
    max-width:1200px;
    margin:0 auto;
    background-color: #1f1f3b;
    background-color: var(--bg-color);
    min-height: 100vh;
    text-align:center;
}
.btn{
    text-decoration: none;
    border:none;
    background: #4db5ff;
    background: var(--prim-blue);
    padding: 5px 10px;
    color: #ffffff;
    border-radius: 8px;
    font-weight: 600;
}
.pageheading{
    font-size: 1.5rem;
    text-transform: uppercase;
}
::-webkit-scrollbar{
    display: none;
}
a{
    color: #4db5ff;
    color: var(--prim-blue);
}
h4,h3{
    color: rgba(255, 255, 255, 0.7);
    color: var(--prim-blue-var-light);
}
h1{
    color: #4db5ff;
    color: var(--prim-blue);

}
